.gpt3__header {
  display: flex;
  flex-direction: row;
}
.gpt3__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-right: 5rem;
}
.gpt3__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
  color: #d1d7e0;
}
.titleS {
  color: #66fcf1;
  font-size: 10rem;
}

.gpt3__header-content p {
  font-family: Manrope;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 27px;

  color: var(--color-text);
  margin-top: 1.5rem;
}

.gpt3__header-content__input {
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
}

.gpt3__header-content__input input {
  flex: 2;
  width: 100%;
  min-height: 50px;
  background: var(--color-footer);
  border: 2px solid var(--color-footer);
  font-family: Manrope;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 27px;
  padding: 0 1rem;

  color: #fff;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.gpt3__header-content__input button {
  flex: 0.6;
  width: 100%;
  min-height: 50px;

  background-color: #222629;
  font-family: Manrope;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  cursor: pointer;
  outline: none;
  padding: 0 1rem;
  background-color: #66fcf1;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
}

.gpt3__header-content__people img {
  width: 20px;
  height: 20px;
}

.gpt3__header-content__people p {
  margin-top: 0;
  margin-left: 0.35rem;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 38px;
  color: #ffffff;
  font-family: var(--font-family);
  text-decoration: underline;
}

.gpt3__header-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gpt3__header-image img {
  width: 90%;
  height: 100%;
  background-color: transparent;
}
.gpt3__header-content__people a {
  cursor: pointer;
}

@media screen and (max-width: 1050px) {
  .gpt3__header {
    flex-direction: column;
  }
  .gpt3__header-content {
    margin: 0 0 3rem;
  }
  .gpt3__header-image img {
    width: 80%;
  }
}
@media screen and (max-width: 660px) {
  .gpt3__header-image img {
    width: 90%;
  }
  .gpt3__header h1 {
    font-size: 45px;
    line-height: 60px;
  }
  .gpt3__header p {
    font-size: 16px;
    line-height: 24px;
  }
  .gpt3__header-content__people {
    flex-direction: column;
  }
  .gpt3__header-content__people p {
    margin: 0;
  }
  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 16px;
    line-height: 24px;
  }
  .titleS {
    font-size: 8rem;
  }
}

@media screen and (max-width: 490px) {
  .gpt3__header h1 {
    font-size: 36px;
    line-height: 48px;
  }
  .gpt3__header p {
    font-size: 14px;
    line-height: 24px;
  }
  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 12px;
    line-height: 16px;
  }
  .titleS {
    font-size: 6rem;
  }
}
